import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { Button, Card, CardBody, CardGroup, Col, Container, Form, Input, InputGroup, InputGroupAddon, InputGroupText, Row, Alert } from 'reactstrap';

class Reset extends Component {
  constructor(props) {
    super(props);
    this.updateInputValue = this.updateInputValue.bind(this);
    this.requestReset = this.requestReset.bind(this);
    this.state = {
      pwd: '',
      repwd: '',
      valid: true
    };
  }

  updateInputValue(evt, val) {
    if (val === 1) {
      this.setState({
        pwd: evt.target.value
      }, () => {
        console.log("PWD:", this.state.pwd);
      });
    } else if (val === 2) {
      this.setState({
        repwd: evt.target.value
      }, () => {
        console.log("REPWD:", this.state.repwd);
      });
    }
  }

  componentDidMount() {
    console.log("COMPONENTS",this.props.match.params.token);
    if(this.props.match.params.token === 'false'){
      this.setState({
        valid: false
      })
    } else{ 
      this.setState({
        valid: true
      })
    }
  }

  requestReset() {
    if(this.state.pwd === this.state.repwd){
      const requestOptions = {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ token: this.props.match.params.token, password: this.state.pwd })
      };
      fetch('https://missionaustralia-back.herokuapp.com/updatePassword', requestOptions)
      .then(response =>  response.json())
      .then(resData => {
         console.log(resData)
         if(resData.data.status === 200){
           alert(resData.data.message);
         } else {
           alert(resData.data.message);
         }
      });
    } else {
      alert("Passwords does not match");
    }
  }

  render() {
    return (
      <div className="app flex-row align-items-center">
        <Container>
          <Row className="justify-content-center">
            <Col md="8">
              <CardGroup>
                <Card className="p-4">
                  <CardBody>
                    <Form>
                      <h1 style={{ textAlign: 'center' }}>Reset Password</h1>
                      {this.state.valid === false ?
                        <Alert color="danger" style={{ textAlign: 'center' }}>
                          Invalid Token
                      </Alert>
                        : null
                      }
                      {this.state.valid === true ?
                        <div>
                          <Alert color="success" style={{ textAlign: 'center' }}>
                            Valid Token
                      </Alert>
                          <InputGroup className="mb-3">
                            <InputGroupAddon addonType="prepend">
                              <InputGroupText>
                              <i className="icon-lock"></i>
                            </InputGroupText>
                            </InputGroupAddon>
                            <Input type="password" placeholder="Password" id="password" value={this.state.pwd} onChange={evt => this.updateInputValue(evt, 1)} />
                          </InputGroup>
                          <InputGroup className="mb-3">
                            <InputGroupAddon addonType="prepend">
                              <InputGroupText>
                              <i className="icon-lock"></i>
                            </InputGroupText>
                            </InputGroupAddon>
                            <Input type="password" placeholder="Re-enter password" id="repassword" value={this.state.repwd} onChange={evt => this.updateInputValue(evt, 2)} />
                          </InputGroup>
                          <Row style={{ textAlign: 'center' }}>
                            <Col xs="12">
                              <Button color="primary" className="px-4" type="button" onClick={this.requestReset}>Update</Button>
                            </Col>
                          </Row>
                          <div style={{ textAlign: 'center' }}><Link to='/login'> Login </Link></div>
                        </div>
                        : null
                      }
                    </Form>
                  </CardBody>
                </Card>
              </CardGroup>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

export default Reset;